'use client';

import { useSession } from 'next-auth/react';
import { useRouter, useSearchParams, usePathname } from 'next/navigation';
import { useEffect, useState, useMemo, useRef, Suspense } from 'react';
import { Card } from '@/components/ui/card';
import dynamic from 'next/dynamic';
import { useHybridCachedData } from '@/utils/useHybridCachedData';
import * as XLSX from 'xlsx';
import DownloadColumnsModal, { ColumnOption } from './components/downloadColumnsModal';
import { handleDownloadPDF } from './components/handleDownloadPDF';
import { permissions } from '@/lib/permissions';

// Icons
import {
  FaUserCog,
  FaCalendarAlt,
  FaFilePdf,
  FaUniversity,
  FaUsers,
  FaEnvelope,
  FaClipboardList,
  FaReply,
  FaChartPie,
  FaHome,
} from 'react-icons/fa';

// Section components (under /components/sections/)
import ProjectsSection from './components/sections/ProjectsSection';
import ReportsSection from './components/sections/ReportsSection';
import EventsSection from './components/sections/EventsSection';
import InstitutionsSection from './components/sections/InstitutionsSection';
import BeneficiariesSection from './components/sections/BeneficiariesSection';
import BeneficiaryMessagesSection from './components/sections/BeneficiaryMessagesSection';
import BeneficiaryRequestsSection from './components/sections/BeneficiaryRequestsSection';
import BeneficiaryRequestResponseSection from './components/sections/BeneficiaryRequestResponseSection';
import ChartsSection from './components/sections/ChartsSection';
import AdminSection from './components/sections/AdminSection';
import HomePageSection from './components/sections/HomePageSection';

// Dynamic form imports (keep as in your original code)
const CreateHomepageForm = dynamic(() => import('./createHomePageForm'), { ssr: false });
const CreateExecutiveMessageForm = dynamic(() => import('./createExecutiveMessageForm'), {
  ssr: false,
});
const CreateBeneficiaryForm = dynamic(() => import('./createBeneficiaryForm'), { ssr: false });
const CreateBeneficiaryMessageForm = dynamic(() => import('./createBeneficiaryMessageForm'), {
  ssr: false,
});
const CreateBeneficiaryRequestForm = dynamic(() => import('./createBeneficiaryRequestForm'), {
  ssr: false,
});
const CreateBeneficiaryRequestResponseForm = dynamic(
  () => import('./createBeneficiaryRequestResponseForm'),
  { ssr: false }
);

const sections = [
  'events',
  'projects',
  'reports',
  'institutions',
  'beneficiaries',
  'beneficiaryMessages',
  'beneficiaryRequests',
  'beneficiaryRequestResponse',
  'charts',
  'Home Page',
  'admin',
] as const;
type Section = (typeof sections)[number];

// Section icons
const sectionIcons: Record<Section, React.ReactNode> = {
  admin: <FaUserCog className="mr-2" />,
  events: <FaCalendarAlt className="mr-2" />,
  reports: <FaFilePdf className="mr-2" />,
  institutions: <FaUniversity className="mr-2" />,
  beneficiaries: <FaUsers className="mr-2" />,
  beneficiaryMessages: <FaEnvelope className="mr-2" />,
  beneficiaryRequests: <FaClipboardList className="mr-2" />,
  beneficiaryRequestResponse: <FaReply className="mr-2" />,
  charts: <FaChartPie className="mr-2" />,
  'Home Page': <FaHome className="mr-2" />,
  projects: <FaClipboardList className="mr-2" />,
};

const rowsPerPageOptions = [5, 10, 25, 50];

const permKeyMap: Record<Section, keyof typeof permissions> = {
  projects: 'Project',
  reports: 'Report',
  admin: 'User',
  events: 'Event',
  institutions: 'Institution',
  beneficiaries: 'Beneficiary',
  beneficiaryMessages: 'BeneficiaryMessage',
  beneficiaryRequests: 'BeneficiaryRequest',
  beneficiaryRequestResponse: 'BeneficiaryRequestResponse',
  charts: 'Project',
  'Home Page': 'HomePage',
};

function canView(type: keyof typeof permissions, role: string) {
  return permissions[type]?.view?.includes(role);
}

export default function AdminDashboardPage() {
  // State and hooks
  const { data: session, status } = useSession();
  const router = useRouter();
  const [activeSection, setActiveSection] = useState<Section | null>(null);
  const [data, setData] = useState<any[]>([]);
  const [editRecord, setEditRecord] = useState<any | null>(null);
  const [showCreateForm, setShowCreateForm] = useState<boolean>(false);
  const [showExecMsgForm, setShowExecMsgForm] = useState<boolean>(false);
  const [sortBy, setSortBy] = useState('createdAt');
  const [sortOrder, setSortOrder] = useState<'asc' | 'desc'>('desc');
  const [rowsPerPage, setRowsPerPage] = useState<number>(10);
  const [page, setPage] = useState<number>(1);
  const [search, setSearch] = useState('');
  const sectionRef = useRef(activeSection);

  const [showCreateBeneficiaryMessageForm, setShowCreateBeneficiaryMessageForm] = useState(false);
  const [showCreateBeneficiaryRequestForm, setShowCreateBeneficiaryRequestForm] = useState(false);
  const [selectedBeneficiaryId, setSelectedBeneficiaryId] = useState<string | null>(null);

  const [showRespondForm, setShowRespondForm] = useState(false);
  const [respondRequestId, setRespondRequestId] = useState<string | null>(null);

  const searchParams = useSearchParams();
  const pathname = usePathname();

  const messagesRef = useRef<HTMLDivElement>(null);
  const homepageRef = useRef<HTMLDivElement>(null);
  const defaultRef = useRef<HTMLDivElement>(null);

  const [downloadModalOpen, setDownloadModalOpen] = useState(false);
  const [downloadColumns, setDownloadColumns] = useState<ColumnOption[]>([]);
  const [pendingDownloadData, setPendingDownloadData] = useState<any[]>([]);

  const [responsesCountMap, setResponsesCountMap] = useState<Record<string, number>>({});
  const [messagesCountMap, setMessagesCountMap] = useState<Record<string, number>>({});
  const [requestsCountMap, setRequestsCountMap] = useState<Record<string, number>>({});

  const {
    data: homepageData,
    isLoading: loadingHomepage,
    refresh: refreshHomepage,
  } = useHybridCachedData('admin-dashboard-homepage-v1', async () => {
    const res = await fetch('/api/homepage-content');
    if (!res.ok) throw new Error('Failed to fetch homepage data');
    const d = await res.json();
    return d ? [d] : [];
  });

  const {
    data: messagesData,
    isLoading: loadingMessages,
    refresh: refreshMessages,
  } = useHybridCachedData('admin-dashboard-messages-v1', async () => {
    const res = await fetch('/api/executive-messages');
    if (!res.ok) throw new Error('Failed to fetch messages data');
    return await res.json();
  });

  useEffect(() => {
    if (activeSection === 'beneficiaryRequests' && data.length > 0) {
      fetch('/api/beneficiaryRequestResponse')
        .then((res) => res.json())
        .then((responses) => {
          const map: Record<string, number> = {};
          responses.forEach((resp: any) => {
            const reqId = resp.responseId;
            map[reqId] = (map[reqId] || 0) + 1;
          });
          setResponsesCountMap(map);
        })
        .catch(() => {});
    }
  }, [activeSection, data]);

  useEffect(() => {
    if (activeSection === 'beneficiaries') {
      fetch('/api/beneficiaryMessages')
        .then((res) => res.json())
        .then((messages) => {
          const map: Record<string, number> = {};
          messages.forEach((msg: any) => {
            if (msg.beneficiaryId) {
              map[msg.beneficiaryId] = (map[msg.beneficiaryId] || 0) + 1;
            }
          });
          setMessagesCountMap(map);
        });

      fetch('/api/beneficiaryRequests')
        .then((res) => res.json())
        .then((requests) => {
          const map: Record<string, number> = {};
          requests.forEach((req: any) => {
            if (req.beneficiaryId) {
              map[req.beneficiaryId] = (map[req.beneficiaryId] || 0) + 1;
            }
          });
          setRequestsCountMap(map);
        });
    }
  }, [activeSection]);

  useEffect(() => {
    if (status === 'unauthenticated') {
      router.replace('/admin');
    }
  }, [status, router]);

  useEffect(() => {
    const searchType = searchParams.get('type') as Section | null;
    const defalultSection: Section = 'charts';
    const sectionToLoad =
      searchType && sections.includes(searchType) ? searchType : defalultSection;
    handleCardClick(sectionToLoad);
    // eslint-disable-next-line
  }, []);

  function norm(v?: string) {
    return (v ?? '').trim().toLowerCase();
  }

  function filterOwn(section: string, rows: any[]) {
    const u = session?.user;
    if (!u) return [];
    if (u.role !== 'beneficiary') return rows;

    const meFirst = norm(u.firstName);
    const meLast = norm(u.lastName);

    if (section === 'beneficiaries') {
      return rows.filter((r: any) => norm(r.firstName) === meFirst && norm(r.lastName) === meLast);
    }

    if (section === 'beneficiaryMessages' || section === 'beneficiaryRequests') {
      return rows.filter((r: any) => {
        const b = r?.beneficiary ?? {};
        return norm(b.firstName) === meFirst && norm(b.lastName) === meLast;
      });
    }

    if (section === 'beneficiaryRequestResponse') {
      return rows.filter((r: any) => {
        const b = r?.request?.beneficiary ?? {};
        return norm(b.firstName) === meFirst && norm(b.lastName) === meLast;
      });
    }

    return rows;
  }

  async function handleCardClick(section: Section) {
    const newUrl = `${pathname}?type=${section}`;
    router.replace(newUrl);
    setActiveSection(section);
    setEditRecord(null);
    setShowCreateForm(false);
    setShowExecMsgForm(false);
    setShowCreateBeneficiaryMessageForm(false);
    setShowCreateBeneficiaryRequestForm(false);
    setSelectedBeneficiaryId(null);
    setShowRespondForm(false);
    setRespondRequestId(null);
    setPage(1);
    sectionRef.current = section;

    const feat = sectionFeatures[section];
    if (section === 'Home Page') {
      // nothing, handled below
    } else if (feat?.apiRoute) {
      try {
        const res = await fetch(feat.apiRoute);
        if (!res.ok) {
          setData([]);
          return;
        }
        const rawData = await res.json();
        setData(filterOwn(section, rawData));
      } catch {
        setData([]);
      }
    } else {
      setData([]);
    }
  }

  function handleEdit(record: any) {
    if (
      activeSection === 'Home Page' &&
      messagesData &&
      messagesData.some((msg: any) => msg.id === record.id)
    ) {
      setEditRecord(record);
      setShowExecMsgForm(true);
      setShowCreateForm(false);
    } else if (
      activeSection === 'Home Page' &&
      homepageData &&
      homepageData.some((home: any) => home.id === record.id)
    ) {
      setEditRecord(record);
      setShowCreateForm(true);
      setShowExecMsgForm(false);
    } else {
      setEditRecord(record);
      setShowCreateForm(false);
      setShowExecMsgForm(false);
    }
  }

  async function handleDelete(
    type:
      | 'projects'
      | 'reports'
      | 'users'
      | 'events'
      | 'institutions'
      | 'beneficiaries'
      | 'beneficiaryMessages'
      | 'beneficiaryRequests'
      | 'beneficiaryRequestResponse'
      | 'homepage-content'
      | 'executive-messages',
    id: string | number
  ) {
    const typeLabel = type.replace('-', ' ').replace(/s$/, '');
    if (!confirm(`Are you sure you want to delete this ${typeLabel}?`)) return;

    try {
      const res = await fetch(`/api/${type}/${id}`, { method: 'DELETE' });

      if (res.ok) {
        setData((prev) => prev.filter((item) => item.id !== id));
        if (type === 'homepage-content') refreshHomepage();
        if (type === 'executive-messages') refreshMessages();
      } else {
        const data = await res.json();
        alert(data.error || `Failed to delete ${typeLabel}. Please try again`);
      }
    } catch (error) {
      console.error(`Error deleting ${typeLabel}:`, error);
      alert(`An error occurred while deleting the ${typeLabel}.`);
    }
  }

  // Section features for columns and API routes
  const sectionFeatures = {
    // ... same as your original sectionFeatures object ...
  };

  const role = session?.user?.role ?? 'guest';
  const activePermKey = activeSection ? permKeyMap[activeSection] : undefined;
  const canViewThisSection = activePermKey ? canView(activePermKey, role) : true;

  // Defensive array handling
  const sortedData = useMemo(() => {
    if (!data) return [];
    if (!activeSection || !sectionFeatures[activeSection]?.sortable) return data ?? [];
    let sorted = [...data];
    sorted.sort((a, b) => {
      let valA = a[sortBy];
      let valB = b[sortBy];
      if (sortBy === 'createdAt' || sortBy === 'updatedAt') {
        valA = new Date(valA).getTime();
        valB = new Date(valB).getTime();
      }
      if (valA < valB) return sortOrder === 'asc' ? -1 : 1;
      if (valA > valB) return sortOrder === 'asc' ? 1 : -1;
      return 0;
    });
    return sorted;
  }, [data, sortBy, sortOrder, activeSection]);

  const filteredData = useMemo(() => {
    if (!sortedData) return [];
    if (!activeSection || !sectionFeatures[activeSection]?.searchable || !search.trim())
      return sortedData ?? [];
    const lower = search.toLowerCase();
    return sortedData.filter((item) =>
      Object.keys(item).some(
        (key) => typeof item[key] === 'string' && item[key].toLowerCase().includes(lower)
      )
    );
  }, [sortedData, search, activeSection]);

  const pageCount = Math.ceil((filteredData?.length ?? 0) / rowsPerPage);
  const paginatedData = useMemo(
    () => (filteredData ?? []).slice((page - 1) * rowsPerPage, page * rowsPerPage),
    [filteredData, page, rowsPerPage]
  );

  // TableActions
  const TableActions = ({
    data,
    columns,
    tableRef,
  }: {
    data: any[];
    columns: any[];
    tableRef: React.RefObject<HTMLDivElement>;
  }) => {
    const handleExportExcel = () => {
      const headers = columns
        .filter((col) => col.header && col.header !== 'Actions' && col.header !== 'Delete')
        .map((col) => (typeof col.header === 'function' ? col.header({}) : col.header));
      const exportableCols = columns.filter(
        (col) => col.header && col.header !== 'Actions' && col.header !== 'Delete'
      );
      const rows = data.map((row, idx) =>
        exportableCols.map((col) => {
          if (col.id === 'number') return idx + 1;
          return row[col.id || col.accessorKey] ?? '';
        })
      );
      const ws = XLSX.utils.aoa_to_sheet([headers, ...rows]);
      const wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');
      XLSX.writeFile(wb, 'table.xlsx');
    };

    return (
      <span className="ml-2 inline-flex gap-2">
        <button
          className="px-2 py-1 border rounded text-sm"
          title="Export to Excel"
          onClick={handleExportExcel}
        >
          Export to Excel
        </button>
        <button
          className="px-2 py-1 border rounded text-sm"
          title="Download as PDF"
          onClick={() => {
            const columnOptions: ColumnOption[] = columns
              .filter((col) => col.header && col.header !== 'Actions' && col.header !== 'Delete')
              .map((col) => ({
                id: col.id || col.accessorKey,
                label: typeof col.header === 'function' ? col.header({}) : col.header,
              }));
            setDownloadColumns(columnOptions);
            setPendingDownloadData(data);
            setDownloadModalOpen(true);
          }}
        >
          Download as PDF
        </button>
      </span>
    );
  };

  // Render active section
  function renderSection() {
    switch (activeSection) {
      case 'projects':
        return (
          <ProjectsSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleDelete={(id) => handleDelete('projects', id)}
            TableActions={TableActions}
          />
        );
      case 'reports':
        return (
          <ReportsSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleDelete={(id) => handleDelete('reports', id)}
            TableActions={TableActions}
          />
        );
      case 'events':
        return (
          <EventsSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleDelete={(id) => handleDelete('events', id)}
            TableActions={TableActions}
          />
        );
      case 'institutions':
        return (
          <InstitutionsSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleDelete={(id) => handleDelete('institutions', id)}
            TableActions={TableActions}
          />
        );
      case 'beneficiaries':
        return (
          <BeneficiariesSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleDelete={(id) => handleDelete('beneficiaries', id)}
            messagesCountMap={messagesCountMap}
            requestsCountMap={requestsCountMap}
            currentUserRole={role}
            TableActions={TableActions}
            onAddMessage={(id: string) => {
              setSelectedBeneficiaryId(id);
              setShowCreateBeneficiaryMessageForm(true);
            }}
            onAddRequest={(id: string) => {
              setSelectedBeneficiaryId(id);
              setShowCreateBeneficiaryRequestForm(true);
            }}
          />
        );
      case 'beneficiaryMessages':
        return (
          <BeneficiaryMessagesSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleDelete={(id) => handleDelete('beneficiaryMessages', id)}
            currentUserRole={role}
            TableActions={TableActions}
          />
        );
      case 'beneficiaryRequests':
        return (
          <BeneficiaryRequestsSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleDelete={(id) => handleDelete('beneficiaryRequests', id)}
            responsesCountMap={responsesCountMap}
            TableActions={TableActions}
            onRespond={(id: string) => {
              setRespondRequestId(id);
              setShowRespondForm(true);
            }}
          />
        );
      case 'beneficiaryRequestResponse':
        return (
          <BeneficiaryRequestResponseSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleDelete={(id) => handleDelete('beneficiaryRequestResponse', id)}
            TableActions={TableActions}
          />
        );
      case 'charts':
        return <ChartsSection />;
      case 'admin':
        return (
          <AdminSection
            paginatedData={paginatedData}
            page={page}
            rowsPerPage={rowsPerPage}
            handleEdit={handleEdit}
            handleDelete={(id) => handleDelete('users', id)}
            TableActions={TableActions}
          />
        );
      case 'Home Page':
        return (
          <HomePageSection
            homepageData={homepageData}
            messagesData={messagesData}
            handleEdit={handleEdit}
            handleDelete={handleDelete}
            showExecMsgAddButton={
              activeSection === 'Home Page' && !showCreateForm && !showExecMsgForm && !editRecord
            }
            onAddExecMsg={() => {
              setEditRecord(null);
              setShowExecMsgForm(true);
              setShowCreateForm(false);
            }}
            TableActions={TableActions}
            homepageRef={homepageRef}
            messagesRef={messagesRef}
          />
        );
      default:
        return null;
    }
  }

  // Main render
  if (status === 'loading') {
    return <div>Checking session ...</div>;
  }

  return (
    <div className="p-6">
      <div className="flex flex-wrap gap-4 mb-4">
        {sections.map((section) => (
          <Card
            key={section}
            onClick={() => handleCardClick(section)}
            className={`
              cursor-pointer 
              px-4 py-3
              flex items-center justify-center text-center
              rounded-sm  
              whitespace-nowrap
              font-medium
              transition 
              ${
                activeSection === section
                  ? 'bg-[#9f004d] text-white shadow-2xl border-2 border-[#9f004d] scale-105'
                  : 'text-gray-400 opacity-70 grayscale hover:opacity-100 hover:grayscale-0'
              }
              `}
            style={{ width: 'auto', minWidth: '3rem', maxWidth: '100%' }}
          >
            <span className="flex items-center">
              {sectionIcons[section]}
              {section.replace(/([A-Z])/g, ' $1').toUpperCase()}
            </span>
          </Card>
        ))}
      </div>
      {activeSection && <div className="mt-6">{renderSection()}</div>}

      {/* Pagination */}
      {pageCount > 1 &&
        !showCreateForm &&
        !showExecMsgForm &&
        !editRecord &&
        !showCreateBeneficiaryMessageForm &&
        !showCreateBeneficiaryRequestForm &&
        !showRespondForm &&
        activeSection !== 'Home Page' &&
        canViewThisSection && (
          <div className="flex gap-2 mt-4 justify-end items-center">
            <button
              className="px-2 py-1 rounded border"
              disabled={page === 1}
              onClick={() => setPage(page - 1)}
            >
              Prev
            </button>
            <span>
              Page {page} of {pageCount}
            </span>
            <button
              className="px-2 py-1 rounded border"
              disabled={page === pageCount}
              onClick={() => setPage(page + 1)}
            >
              Next
            </button>
          </div>
        )}
      {/* Download Modal */}
      <DownloadColumnsModal
        isOpen={downloadModalOpen}
        onClose={() => setDownloadModalOpen(false)}
        columns={downloadColumns}
        onDownload={(selectedColumns) => {
          handleDownloadPDF(
            pendingDownloadData,
            selectedColumns,
            activeSection ? activeSection.charAt(0).toUpperCase() + activeSection.slice(1) : 'Data'
          );
        }}
      />
    </div>
  );
}
